import React from 'react';
import './sub.css'

const Subject=props=>{
    return(
        <div className="subject text-center">
            <div className="Overflow">
                <img src={props.imgsrc} alt="sub_pic" width="200px" height="200px" />
            </div>
            <div className="sub-detail text-dark">
                <h4 className="sub-title">{props.name}</h4>
                <p className="sub-credit text-secondary">
                    <h6>Credits</h6>{props.credit}
                </p>
                <p className="sub-sem text-secondary">
                    <h6>Semester</h6>{props.sem} 
                </p>
            </div>
        </div>
    );

};

export default Subject;