import React,{Component} from 'react';
import Card from './sub_card.js';
import sub_pic1 from "./sub_pics/pds.jpg";
import sub_pic3 from "./sub_pics/embedded.jpg";
import sub_pic4 from "./sub_pics/HPCA.png";
import sub_pic5 from "./sub_pics/cs.jpg";
import './sub.css';

class Cards extends Component{
    render(){
        return(
            <div className="container-fluid d-flex justify-content-center">
                <div className="row">
                    
                    <div className="col">
                        <a href="#heading_subject1">
                            <Card imgsrc={sub_pic1} name="P.D.S. Sec-4" credit="3" sem="SPRING"/>
                        </a>
                    </div>
                    
                    <div className="col"> 
                        <a href="#heading_subject2">
                            <Card imgsrc={sub_pic1} name="P.D.S. Sec-15" credit="3" sem="AUTUMN"/>
                        </a>
                    </div>
                    
                    <div className="col">
                        <a href="#heading_subject3">
                            <Card imgsrc={sub_pic3} name="Embedded System" credit="2" sem="SPRING"/>
                        </a>
                    </div>
                    <div className="col">
                        <a href="#heading_subject4">
                            <Card imgsrc={sub_pic4} name="H.P.C.A." credit="4" sem="AUTUMN"/>
                        </a>
                    </div>

                    <div className="col">
                        <a href="#heading_subject5">
                            <Card imgsrc={sub_pic5} name="Computing LAB" credit="3" sem="SPRING"/>
                        </a>
                    </div>
                </div>
            </div>
        );
    };
}

export default Cards;