import React,{Component} from 'react';
import Student from './student.js'
import './stu.css';

class Subfour extends React.Component{
    
    state={
        students: [
            {name:'Kabir', roll:'16CE31027'},
            {name:'Kira', roll:'16CE10039'},
            {name:'Samy', roll:'16CE31011'},
            {name:'Nira', roll:'16CE10040'},
            {name:'Shobhit', roll:'16CE31021'}
        ]
    }

    deleteStudent=(index, e)=>{
        const students=Object.assign([], this.state.students);
        students.splice(index, 1);
        this.setState({students:students});
        alert("Student is getting removed, Refresh to undo !");
    }

    render(){
        return(
            <div>
                <ul>
                {
                    this.state.students.map( (user, index)=>{
                    return(
                        <Student roll={user.roll} delStudent={this.deleteStudent.bind(this, index)}name={user.name}/>
                    );
                    }
                    )
                }
                </ul>
            </div>
        );
    }
}

export default Subfour;