import React from 'react';
import './stu.css';

const Student=(props)=>{
    return(
        <div>
            <li className="student">
                <span>
                    <div className="name">{props.name}</div>  
                    <div className="roll">{props.roll}</div>
                </span>
                <button onClick={props.delStudent} className="del">Delete </button>
            </li>
        </div>
    );
}

export default Student;